.conversejs {
    converse-muc {

        .muc-bottom-panel,
        converse-muc-bottom-panel {
            display: contents;
            padding: 0.5em;
            text-align: center;
            font-size: var(--font-size-small);
            background-color: var(--background-color);
            color: var(--foreground-color);

            &.muc-bottom-panel--muted {
                border-top: 0.25em solid var(--muc-color) !important;
                display: block;
                height: 6em;
                padding-top: 2em;
                width: 100%;
            }

            &.muc-bottom-panel--nickname {
                padding: 0;
                height: 16em;

                .muc-form-container {
                    .chatroom-form {
                        padding-top: 2em;
                        padding-bottom: 0;
                    }
                }
            }
        }

        .muc-bottom-panel {
            height: 3em;
        }

        converse-muc-bottom-panel {
            .chat-message-form {
                border-top: 0.25em solid var(--muc-color) !important;
                border-inline-end: 0 !important;

                .suggestion-box__results--above {
                    bottom: 4.5em;
                }
                .chat-textarea, input {
                    outline: 1px solid var(--background-color) !important;
                    &:active, &:focus{
                        outline-width: 2px !important;
                    }
                    &.correcting {
                        background-color: var(--chatroom-correcting-color);
                    }
                }
                .chat-textarea {
                    width: 100%;
                    border: none;
                    border-bottom-right-radius: 0;
                    field-sizing: content;
                }
            }
        }
    }
}
